.card {
  height: 100px;
  border: 1px solid blue;
  transition: 0.7s;
  flex: 1 1 30%;
  margin: 5px;
  text-align: center;
}
.card:hover {
  box-shadow: 0 5px 8px 0 rgba(0, 0, 0, 0.5);
}
.App {
  margin: auto;
  width: 50%;
}
.form {
  width: 100%;
  color: #595f6e;
  height: 1.5rem;
}
